import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SharedModule } from '../../shared/shared.module';

import { DataTableModule } from 'angular-4-data-table-fix';
import { FeederSummaryComponent } from './feederSummary/feederSummary.component';
import { PlannedVsActualComponent } from './plannedVsActual/plannedVsActual.component';
import { InvoiceComponent } from './invoice/invoice.component';
import { FeaturewisefieldvsGIS } from './featurewisefieldvsGIS/featurewisefieldvsGIS.component';
import { GridStatusComponent } from './gridStatus/gridStatus.component';
import {ExportToCsvService} from '../../core/services/export.service';
import{Ng2SmartTableModule}from'ng2-smart-table'; 
import { ReportService } from './service/report.service';

const routes: Routes = [
    { path: '', redirectTo: 'reports' },
    { path: 'feederSummary', component: FeederSummaryComponent },
    { path: 'plannedVsActual', component: PlannedVsActualComponent },
    { path: 'invoice', component: InvoiceComponent },
    { path: 'featurewisefieldvsGIS', component: FeaturewisefieldvsGIS },
    { path: 'gridStatus', component: GridStatusComponent }
];

@NgModule({
    imports: [
        SharedModule,
        RouterModule.forChild(routes),
        DataTableModule,
        Ng2SmartTableModule
    ],
    declarations: [
        FeederSummaryComponent,
        PlannedVsActualComponent,
        InvoiceComponent,
        FeaturewisefieldvsGIS,
        GridStatusComponent
    ],
    exports: [
        RouterModule
    ],
    providers:[ExportToCsvService,ReportService]
})
export class ReportsModule { }



